library.add(faAngleUp, faAngleDown)
export default {
  components: {
    // Carousel: () =>
    //   import('vue-carousel')
    //     .then((m) => m.Carousel)
    //     .catch(),
    // Slide: () =>
    //   import('vue-carousel')
    //     .then((m) => m.Slide)
    //     .catch(),
    FontAwesomeIcon,
    // SiteNotice,
    // ScoreRank,
    // FriendLinks,
    // TweetsWidget,
    // TopicsNav,
    RentRecommend,
    TopicList,
    Pagination2,
    MetroSelector,
    NoTip
  },
  async asyncData({ $axios, query, req }) {
    let city = '北京'
    if ($axios.defaults.headers.common.cookie) {
      const cookies = cookie.parse($axios.defaults.headers.common.cookie || '')
      if (cookies && cookies !== '') {
        city = cookies.city
      }
    } else {
      city = utils.getCity()
    }
    try {
      const [topicsPage, subwaysAndRegions] = await Promise.all([
        $axios.get('/api/topic/rent/topics', {
          params: {
            page: query.p || 1,
            city,
            nodeId: 2
          }
        }),
        $axios.get('/api/topic/subway', {
          params: {
            city
          }
        })
        // $axios.get('/api/user/score/rank'),
        // $axios.get('/api/link/toplinks'),
        // $axios.get('/api/tweet/newest')
      ])

      return { topicsPage, subwaysAndRegions }
    } catch (e) {
      console.error(e)
    }
  },
  data() {
    return {
      searchTerms: '',
      searchAction: false,
      rentPagingAction: '/api/topic/rent/topics',
      geoPagingAction: '/api/topic/geo/topics',
      fullGeoPagingAction: '/api/topic/full/geo/topics',
      pageAction: '',
      pageParmsContext: {},
      showMetraDiv: false,
      selectStation: {},
      showRentRecommentDiv: false,
      dtype: 0
    }
  },
  mounted() {
    if (utils.getCity() === '武汉') {
      this.showMetraDiv = true
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6183'
    }
    if (utils.getCity() === '北京') {
      this.showMetraDiv = true
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6346'
    }
    if (utils.getCity() === '杭州') {
      this.bannerImgurl =
        'https://upload-images.jianshu.io/upload_images/17856673-382c2bfa30d1d02f.JPG'
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6183'
    }
    if (utils.getCity() === '上海') {
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6347'
    }

    this.pageAction = this.rentPagingAction
  },
  methods: {
    onPageChanged(pageAction, page, parmsContext) {
      let params = {}

      if (pageAction === this.rentPagingAction) {
        params = this.getNodeTopicParms(page)
      }

      if (pageAction === this.fullGeoPagingAction) {
        params = this.getRentParms(
          this.dtype,
          page,
          this.pageParmsContext.dataId
        )
      }

      if (pageAction === this.geoPagingAction) {
        params = this.getGeoStationParms(
          this.dtype,
          page,
          this.pageParmsContext.geoStationId
        )
      }
      this.changePageTopics(pageAction, params, null)

      setTimeout(() => {
        window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })
      }, 300)
    },
    getSelectStationObject(geohash) {
      return {
        geoStationId: -1,
        geoHash: geohash
      }
    },
    getRentParms(dtype, page, dataId) {
      return {
        page: page || 1,
        city: utils.getCity(),
        dataId,
        dtype
      }
    },
    getNodeTopicParms(page) {
      return {
        page: page || 1,
        city: utils.getCity(),
        nodeId: 2
      }
    },
    getGeoStationParms(dtype, page, geoStationId) {
      return {
        page: page || 1,
        city: utils.getCity(),
        geoStationId,
        dtype
      }
    },
    onChildStationChanged(dtype, item, parent) {
      if (item.name === '不限') {
        this.showRentRecommentDiv = false
        this.onParentStationChanged(dtype, parent)
      } else {
        this.showRentRecommentDiv = true
        this.refreshRentRecommend()
        this.selectStation = item
        this.pageAction = this.geoPagingAction
        this.pageParmsContext = this.getGeoStationParms(
          dtype,
          1,
          item.geoStationId
        )
        this.changePageTopics(
          this.geoPagingAction,
          this.pageParmsContext,
          () => {
            this.refreshPagination()
          }
        )
      }
    },
    onRecommendStationChanged(dtype, geoStationId) {
      this.pageAction = this.geoPagingAction
      this.pageParmsContext = this.getGeoStationParms(dtype, 1, geoStationId)
      this.changePageTopics(this.geoPagingAction, this.pageParmsContext, () => {
        this.refreshPagination()
      })
    },
    onParentStationChanged(dtype, item) {
      this.dtype = dtype
      this.showRentRecommentDiv = false
      if (item.name === '不限') {
        this.changePageTopics(
          this.rentPagingAction,
          this.getNodeTopicParms(1),
          null
        )
      } else {
        this.pageAction = this.fullGeoPagingAction
        this.pageParmsContext = this.getRentParms(dtype, 1, item.id)
        this.changePageTopics(
          this.fullGeoPagingAction,
          this.pageParmsContext,
          () => {
            this.refreshPagination()
          }
        )
      }
    },
    onTabChanged(index) {
      // 切换地铁 or 行政 tab
      if (
        this.rentPagingAction !== this.pageAction ||
        this.topicsPage.page.page > 1
      ) {
        this.showRentRecommentDiv = false
        this.changePageTopics(
          this.rentPagingAction,
          this.getNodeTopicParms(1),
          null
        )
      }
    },
    // onSectionChanged(item) {
    //   if (item.name === '不限') {
    //     this.pageAction = this.rentPagingAction
    //     this.pageParmsContext = this.getRentParms(1, item.dataId)
    //     this.changePageTopics(
    //       this.rentPagingAction,
    //       this.pageParmsContext,
    //       () => {
    //         this.refreshPagination()
    //       }
    //     )
    //   } else {
    //     this.geoSearchTopics(item)
    //   }
    // },
    onCollapseBack(state) {
      // console.log('onCollapse:' + state)
    },
    jumpCityWechatUrl() {
      window.location.href = this.jumpCityUrl
    },
    jumpUrl(href) {
      if (href) {
        window.open(href)
      } else {
      }
    },
    twitterCreated(data) {
      if (this.topicsPage) {
        if (this.topicsPage.results) {
          this.topicsPage.results.unshift(data)
        } else {
          this.topicsPage.results = [data]
        }
      }
    },
    changePageTopics(url, params, callback) {
      this.pageAction = url
      this.$axios
        .get(url, { params })
        .then((res) => {
          this.topicsPage.results = res.results
          this.topicsPage.page = res.page
          if (callback) {
            callback()
          }
        })
        .catch((e) => {
          console.log('tagSearchTopics error:' + JSON.stringify(e))
        })
    },
    refreshPagination() {
      const state = this.searchAction
      this.searchAction = !this.searchAction
      this.$nextTick(() => {
        this.searchAction = state
      })
    },
    refreshRentRecommend() {
      const state = this.showRentRecommentDiv
      this.showRentRecommentDiv = !this.showRentRecommentDiv
      this.$nextTick(() => {
        this.showRentRecommentDiv = state
      })
    },
    geoSearchTopics(rentItem) {
      this.pageAction = this.geoPagingAction
      this.pageParmsContext = {
        page: 1,
        geoStationId: rentItem.id
      }
      this.changePageTopics(this.geoPagingAction, this.pageParmsContext, () => {
        this.refreshPagination()
      })

      // this.$axios
      //   .get('/api/topic/tag/topiclist', {
      //     params: {
      //       page: 1,
      //       tagName
      //     }
      //   })
      //   .then((res) => {
      //     this.topicsPage.results = res.results
      //     this.topicsPage.page = res.page
      //   })
      //   .catch((e) => {
      //     console.log('tagSearchTopics error:' + JSON.stringify(e))
      //   })
    },
    // async search() {
    //   try {
    //     await this.$axios.post('/api/user/edit/' + this.user.id, {
    //       nickname: this.form.nickname,
    //       avatar: this.form.avatar,
    //       homePage: this.form.homePage,
    //       description: this.form.description
    //     })
    //     await this.reload()
    //     this.$toast.success('资料修改成功')
    //   } catch (e) {
    //     console.error(e)
    //     this.$toast.error('资料修改失败：' + (e.message || e))
    //   }
    // },
    jumpCreateRent() {
      utils.linkTo('/rent/create')
    },
    search() {
      const search = this.searchTerms.trim()
      if (utils.isEmpty(search)) {
        this.$toast.error('请输入地点')
        return
      }

      const that = this

      GeoUtil.requestAmapGeo(this.$axios, search, (geoObject) => {
        const result = GeoUtil.getLatlonFromGeoRequest(geoObject)
        // console.log('requestAmapGeo:' + JSON.stringify(result))
        that.showRentRecommentDiv = true
        that.selectStation = that.getSelectStationObject(result.geohash)
        that.refreshRentRecommend()
      })

      this.$axios
        .get('/api/topic/search', {
          params: {
            terms: search,
            city: utils.getCity()
          }
        })
        .then((res) => {
          this.topicsPage.results = res
          this.searchAction = true
        })
        .catch((e) => {
          console.log('search error:' + JSON.stringify(e))
        })
    }
  },
  head() {
    return {
      title: this.$siteTitle('90社区—出租/转租/合租找室友'),
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.$siteDescription()
        },
        { hid: 'keywords', name: 'keywords', content: this.$siteKeywords() }
      ]
    }
  }
}
