<template>
  <ul class="topic-list topic-wrap">
    <li
      v-for="(topic, index) in topics"
      :key="topic.topicId"
      :index="index"
      class="topic-item"
    >
      <!-- 信息流广告 -->
      <!-- <adsbygoogle
        v-if="showAd && (index === 3 || index === 10 || index === 18)"
        ad-slot="4980294904"
        ad-format="fluid"
        ad-layout-key="-ht-19-1m-3j+mu"
      /> -->
      <article itemscope itemtype="http://schema.org/BlogPosting">
        <div class="topic-header">
          <div class="topic-header-left">
            <a v-if="topic.user.id === -1">
              <img
                alt="90社区"
                src="~/assets/images/douban.png"
                class="avatar"
              />
            </a>
            <a
              v-else
              :href="'/user/' + topic.user.id"
              :title="topic.user.nickname"
            >
              <img :src="topic.user.smallAvatar" class="avatar" />
            </a>
          </div>
          <div class="topic-header-center">
            <h1 class="topic-title" itemprop="headline">
              <a
                v-if="
                  topic.node &&
                    topic.node.nodeId === 2 &&
                    checkRentFromDouban(topic)
                "
                :target="blank"
                :href="topic.href"
                :title="topic.title"
                >{{ topic.title }}</a
              >

              <a
                v-else-if="topic.node && topic.node.nodeId === 1"
                :target="blank"
                :href="'/love/' + topic.topicId"
                :title="topic.title"
                >{{ topic.title }}</a
              >
              <a
                v-else
                :target="blank"
                :href="'/topic/' + topic.topicId"
                :title="topic.title"
                >{{ topic.title }}</a
              >
              <span :class="topic.topType !== 0 ? 'text-top' : ''">{{
                getTopText(topic)
              }}</span>
            </h1>
            <div class="topic-meta">
              <!-- <span
                class="meta-item"
                itemprop="author"
                itemscope
                itemtype="http://schema.org/Person"
              >
                <a :href="'/user/' + topic.user.id" itemprop="name">{{
                  topic.user.nickname
                }}</a>
              </span> -->
              <span class="meta-item">
                <SectionTime
                  :create-time="topic.createTime"
                  :hide="topic.node && topic.node.nodeId === 1"
                ></SectionTime>
                <!-- <time
                  :datetime="
                    topic.createTime | formatDate('yyyy-MM-ddTHH:mm:ss')
                  "
                  itemprop="datePublished"
                  >{{ topic.createTime | prettyDate }}</time
                > -->
              </span>
              <span class="meta-item">
                <!-- <a class="node">{{ topic.node.name }}</a> -->
                <!-- <a
                  v-if="topic.node && topic.node.nodeId === 2"
                  :target="blank"
                  href="/rents"
                  class="node"
                  >{{ topic.node.name }}</a
                >
                <a
                  v-else-if="topic.node && topic.node.nodeId === 1"
                  :target="blank"
                  href="/love"
                  class="node"
                  >{{ topic.node.name }}</a
                >
                <a
                  v-else
                  :href="'/topic/node/' + topic.node.nodeId"
                  :target="blank"
                  class="node"
                  >{{ topic.node.name }}</a
                > -->
              </span>
              <span class="meta-item">
                <span
                  v-for="tag in topic.tags"
                  :key="tag.tagName + tag.tagId"
                  class="tag"
                >
                  <!-- <a :href="'/topics/tag/' + tag.tagId">{{ tag.tagName }}</a> -->
                  <a>{{ tag.tagName }}</a>
                </span>
                <span
                  v-if="
                    topic.node &&
                      topic.node.nodeId === 2 &&
                      !checkRentFromDouban(topic)
                  "
                  :target="blank"
                  class=""
                >
                  <a class="tag" @click="clickCollapse(index)"
                    >展开&nbsp;
                    <font-awesome-icon
                      v-if="!isCollapsedArray[index]"
                      icon="angle-down"
                    />
                    <font-awesome-icon
                      v-if="isCollapsedArray[index]"
                      icon="angle-up"
                    />
                  </a>
                </span>
              </span>
            </div>
          </div>
          <div class="topic-header-right">
            <div class="like">
              <span
                :class="{ liked: topic.liked }"
                class="like-btn"
                @click="like(topic)"
              >
                <i class="iconfont icon-like" />
              </span>
              <span v-if="topic.likeCount" class="like-count">{{
                topic.likeCount
              }}</span>
            </div>
            <span class="count"
              >{{ topic.commentCount }}&nbsp;/&nbsp;{{ topic.viewCount }}</span
            >
          </div>
        </div>
        <div
          class="card-content"
          :class="{ 'is-hidden': isCollapsedArray[index] }"
        >
          <!-- <slot v-if="isCollapsedArray[index]"> -->
          <div
            style=" font-size: 13px;
                color: black;"
            v-html="topic.content"
          ></div>
          <a
            v-if="topic.imageList && topic.imageList.length > 0"
            :target="blank"
            :href="'/rent/' + topic.topicId"
            style=" font-size: 12px;
                color: red;text-decoration:underline;"
          >
            (有图，点击查看)
          </a>
          <div
            v-else
            style=" font-size: 12px;
                color: red;"
          >
            (无图）
          </div>
          <!-- </slot> -->
        </div>
        <!-- <Panel
          id="frontpage-firstpanel"
          title="rr"
          :on-collapsed="onCollapseBack"
          >Some Content
          <template slot="title">
            <div class="footer-buttons">
              <button class="button is-primary is-pulled-left">Button</button>
              <button class="button is-danger is-pulled-right">Oh no!</button>
            </div>
          </template>
        </Panel> -->
      </article>
    </li>
  </ul>
</template>

<script>
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import utils from '~/common/utils'
// import Panel from '~/components/Panel'
import SectionTime from '~/components/SectionTime'

library.add(faAngleUp, faAngleDown)
export default {
  components: {
    // Panel,
    FontAwesomeIcon,
    SectionTime
  },
  props: {
    topics: {
      type: Array,
      default() {
        return []
      },
      required: false
    },
    showAd: {
      type: Boolean,
      default: false
    },
    openNewPage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      blank: '',
      isCollapsed: true,
      isCollapsedArray: []
    }
  },
  watch: {
    topics(newValue, oldValue) {
      if (this.topics && this.topics.length !== undefined) {
        this.isCollapsedArray = new Array(this.topics.length).fill(true)
      }
    }
  },
  mounted() {
    if (this.openNewPage) {
      this.blank = '_blank'
    }
    console.log('topics:' + JSON.stringify(this.topics))
    if (this.topics && this.topics.length > 0) {
      this.isCollapsedArray = new Array(this.topics.length).fill(true)
    }

    // console.log(JSON.stringify(this.isCollapsedArray))
  },
  methods: {
    async clickCollapse(index) {
      if (this.topics[index].content !== undefined) {
      } else {
        try {
          const topic = await this.$axios.get(
            '/api/topic/' + this.topics[index].topicId
          )
          this.topics[index].content = topic.content
          this.topics[index].imageList = topic.imageList
        } catch (e) {}
      }

      this.$set(this.isCollapsedArray, index, !this.isCollapsedArray[index]) // 改变数组

      // console.log('detail:' + JSON.stringify(topic))
    },
    getTopText(topic) {
      const topType = topic.topType ? topic.topType : 0
      return utils.getTopText(topType)
    },
    onCollapseBack(state) {
      // console.log(state)
    },
    checkRentFromDouban(topic) {
      const extra = JSON.parse(topic.extraData)
      // console.log('extra:' + extra)
      if (extra && extra.from === '豆瓣租房') {
        topic.href = extra.href // 同时修改href
        return false
      }
      return false
    },
    async like(topic) {
      this.isCollapsed = !this.isCollapsed
      try {
        await this.$axios.post('/api/topic/like/' + topic.topicId)
        topic.liked = true
        topic.likeCount++
      } catch (e) {
        if (e.errorCode === 1) {
          this.$toast.info('请登录后点赞！！！', {
            action: {
              text: '去登录',
              onClick: (e, toastObject) => {
                utils.toSignin()
              }
            }
          })
        } else {
          this.$toast.error(e.message || e)
        }
      }
    }
  }
}
</script>

<style scoped>
span.text-top {
  padding: 2px;
  background-color: rgba(238, 30, 30, 0.918);
  color: white;
}

