<template>
  <div class="metro-body">
    <div class="tabs is-small is-toggle metro-tabs">
      <ul>
        <li :class="tabIndex === 0 ? 'is-active' : ''">
          <a @click="tabChanged(0)">
            <span>地铁线路</span>
          </a>
        </li>
        <li :class="tabIndex === 1 ? 'is-active' : ''">
          <a @click="tabChanged(1)">
            <span>行政区域</span>
          </a>
        </li>
      </ul>
    </div>
    <div v-if="tabIndex === 0">
      <div class="metro-action-content">
        <div class="metro-label">
          地铁
        </div>
        <div>
          <a
            v-for="(item, index) in subways"
            :key="item.name"
            :class="index === metroIndex ? 'metro-a' : ''"
            :index="index"
            @click="metroIndexChanged(index)"
          >
            {{ item.name }}</a
          >
        </div>
      </div>
    </div>
    <div v-if="tabIndex === 1" class="metro-action-content">
      <div class="metro-label">
        区域
      </div>
      <div>
        <a
          v-for="(item, index) in regions"
          :key="item.name"
          :class="index === sectionIndex ? 'metro-a' : ''"
          :index="index"
          @click="sectionIndexChanged(index)"
        >
          {{ item.name }}</a
        >
      </div>
    </div>
    <div v-if="metroIndex > 0 || sectionIndex > 0" class="metro-a-container">
      <a
        v-for="(item, index) in geoStationList"
        :key="item.id"
        :class="index === geoStationIndex ? 'metro-a' : ''"
        :index="index"
        @click="childStationChanged(index)"
      >
        {{ item.name }}</a
      >
    </div>
  </div>
</template>

<script>
// import "@/storageShim.js";
import cookie from 'cookie'
import StorageUtil from '~/common/StorageUtil'
import utils from '~/common/utils'
export default {
  components: {
    // FontAwesomeIcon
  },
  props: {
    // id: {
    //   type: String
    // },
    // title: {
    //   type: String,
    //   required: false
    // },
    subwaysAndRegions: {
      type: Array,
      default() {
        return []
      },
      required: true
    },
    // onMetroChanged: {
    //   type: Function,
    //   required: true
    // },
    // onSectionChanged: {
    //   type: Function,
    //   required: true
    // },
    onParentStationChanged: {
      type: Function,
      required: true
    },
    onChildStationChanged: {
      type: Function,
      required: true
    },
    onTabChanged: {
      type: Function,
      required: false
    }
  },
  data() {
    return {
      subways: [{ name: '不限', id: -1 }],
      regions: [{ name: '不限', id: -1 }],
      dtype: 0,
      tabIndex: 0,
      metroIndex: 0,
      sectionIndex: 0,
      addressIndex: 0,
      isCollapsed: true,
      geoStationIndex: 0,
      geoStationListAction: '/api/topic/stationlist',
      geoStationList: [{ name: '不限', id: -1 }],
      showDetailView: false
    }
  },
  watch: {
    isCollapsed(newValue) {
      if (this.onCollapsed) {
        this.onCollapsed(newValue)
      }
      // if (this.id) {
      //   this.saveCollapsedState(newValue)
      // }
      // this.$emit('collapseChanged', newValue)
    }
  },
  mounted() {
    // this.isCollapsed = true
    if (this.id) {
      // const state = this.getCollapseState()
      // if (state) {
      //   this.isCollapsed = state[this.id]
      // }
    }
    if (this.subwaysAndRegions) {
      this.subwaysAndRegions.forEach((item) => {
        if (item.dtype === 0) {
          this.subways.push({ name: item.name, id: item.dataId })
        } else {
          this.regions.push({ name: item.name, id: item.dataId })
        }
      })
    }
  },
  methods: {
    metroIndexChanged(index) {
      this.metroIndex = index
      this.geoStationIndex = 0
      const subway = this.subways[index]
      if (this.onParentStationChanged) {
        this.onParentStationChanged(this.dtype, subway)
      }
      // console.log('subway:' + JSON.stringify(subway))
      this.requestStationList(this.dtype, subway.id, utils.getCity())
    },
    childStationChanged(index) {
      this.geoStationIndex = index
      if (this.onChildStationChanged) {
        const geoStation = this.geoStationList[index]
        const parent =
          this.tabIndex === 0
            ? this.subways[this.metroIndex]
            : this.regions[this.sectionIndex]
        this.onChildStationChanged(this.dtype, geoStation, parent)
      }
    },
    sectionIndexChanged(index) {
      this.sectionIndex = index
      this.geoStationIndex = 0

      const region = this.regions[index]
      if (this.onParentStationChanged) {
        this.onParentStationChanged(this.dtype, region)
      }
      this.requestStationList(this.dtype, region.id, utils.getCity())
    },
    requestStationList(dtype, dataId, city) {
      const params = {
        dtype,
        dataId,
        city
      }

      const res = StorageUtil.getStationList(city, dtype, dataId)
      if (res) {
        this.geoStationList = [{ name: '不限', id: -1 }]
        Array.prototype.push.apply(this.geoStationList, res)
      } else {
        // console.log('params : ' + JSON.stringify(params))
        this.$axios
          .get(this.geoStationListAction, { params })
          .then((res) => {
            this.geoStationList = [{ name: '不限', id: -1 }]
            Array.prototype.push.apply(this.geoStationList, res)

            if (this.geoStationList && this.geoStationList.length > 0) {
              StorageUtil.setStationList(city, dtype, dataId, res)
            }
          })
          .catch((e) => {
            console.log('tagSearchTopics error:' + JSON.stringify(e))
          })
      }
    },
    tabChanged(index) {
      this.tabIndex = index
      this.dtype = index
      this.geoStationList = []
      this.metroIndex = 0
      this.sectionIndex = 0

      if (this.onTabChanged) {
        this.onTabChanged(index)
      }

      this.changeDetailView()
    },
    getCollapseState() {
      return this.isCollapsed
      // return {}
    },
    changeDetailView() {
      if (this.tabIndex === 0) {
        this.showDetailView = this.metroIndex !== 0
      }

      if (this.tabIndex === 1) {
        this.showDetailView = this.metroIndex !== 0
      }
    },
    saveCollapsedState(value) {
      // console.log(value)
      const state = this.getCollapseState()
      // state[this.id] = value
      // localStorage.setObject('collapsibles', state)
    }
  }
}
</script>

